import { SET_MODE, SET_PLAYER1, SET_PLAYER2, SET_LEVEL } from "./tictac.action";

const initialState = {};
const TictacReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_MODE:
      return {
        ...state,
        mode: action.payload,
      };
    case SET_PLAYER1:
      return {
        ...state,
        player1: action.payload,
      };
    case SET_PLAYER2:
      return {
        ...state,
        player2: action.payload,
      };
    case SET_LEVEL:
      return {
        ...state,
        level: action.payload,
      };
    default:
      return state;
  }
};

export default TictacReducer;
